@charset "utf-8"; /* 声明编码集 */
/*!herder*/
.header {
    height: 90px;
    img {
        float: left;
        margin-top: 24px;
        width: 108px;
        height: 48px;
    }
}
/*!content*/
.content {
    background: linear-gradient(to right, #17073d, #17073d, #17073d);
    height: 600px;
    .ad {
        float: left;
        margin-top: 90px;
        img {
            width: 720px;
            height: 400px;
        }
    }
    .logon {
        width: 350px;
        background: #fff;
        margin: 90px auto 10px;
        padding: 0;
        overflow: hidden;
        float: right;
        min-height: 310px;
        text-align: center;
        position: relative;
        .user div,
        .pass div {
            width: 46px;
            height: 46px;
            line-height: 40px;
            text-align: center;
            color: #fff;
            position: absolute;
            background-color: #ccc;
            left: 61px;
        }
        .user input,
        .pass input {
            border: 1px solid #ccc;
            height: 42px;
            outline: none;
            font-size: 14px;
            padding-left: 50px;
            margin-top: 25px;
        }
        .user {
            div {
                top: 25px;
            }
        }
        .pass {
            div {
                top: 96px;
            }
        }
        .btn {
            width: 230px;
            height: 42px;
            background: #ff1268;
            text-align: center;
            margin-left: 61px;
            margin-top: 25px;
            border: 1px solid #ff9000;
            border-radius: 3px;
            font-size: 20px;
            line-height: 42px;
            outline: none;
            color: #fff;
            cursor: pointer;
        }
        a {
            display: inline-block;
            font-size: 14px;
            position: absolute;
            bottom: 40px;
            right: 30px;
            cursor: pointer;
            color: rgb(35, 168, 221);
        }
    }
}
